<template>
  <div class="index">
    <Header></Header>
    <div class="container">
      <Aside></Aside>
      <Main></Main>
    </div>
  </div>
</template>

<script>
import Header from '@/components/Header'
import Aside from '@/components/Aside'
import Main from '@/components/Main'
export default {
  name: 'index',
  data () {
    return {

    }
  },
  components: {
    'Aside': Aside,
    'Header': Header,
    'Main': Main
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.index{
  height: 100%;
  display: flex;
  flex-direction: column;
}
.index .container{
  display: flex;
  flex-grow: 1;
}
.index .container .aside{
  
}
.index .container .main{
  flex-grow: 1;
}
</style>



