<template>
	<div class="aside">
		<el-menu class='el-menu' default-active="work" :collapse="isCollapse" active-text-color="#ea6f5a" router>
      <el-menu-item index="work">
				<i class="el-icon-menu"></i>
        <span slot='title'>工作台</span>
      </el-menu-item>
      <el-menu-item index="task">
				<i class="el-icon-bell"></i>
         <span slot='title'>任务</span>
      </el-menu-item>
      <el-menu-item index="project">
				<i class="el-icon-star-off"></i>
        <span slot='title'>项目</span>
      </el-menu-item>
      <el-menu-item index="member">
				<i class="el-icon-share"></i>
        <span slot='title'>成员</span>
      </el-menu-item>
    </el-menu>
    <div class="toggle">
    	<el-button v-if="isCollapse" v-on:click="isCollapse=!isCollapse"><i class="el-icon-arrow-right"></i></el-button>
    	<el-button v-else v-on:click="isCollapse=!isCollapse"><i class="el-icon-arrow-left"></i></el-button>
    </div>
	</div>
</template>
<script>
	export default{
		name: 'Aside',
		data(){
			return{
				isCollapse:false
			}
		}
	}
</script>
<style scoped>
	.aside{
		display: flex;
		flex-direction: column;
	}
	.el-menu{
		background: #f7f7f7;
		flex-grow: 1;
		overflow-y: auto;
	}
	.el-menu:not(.el-menu--collapse){
		width: 220px;
	}
	.el-submenu{
		background: #f7f7f7;
	}
	.el-menu-item.is-active{
		border-left: 2px solid #ea6f5a;
	}
	.toggle .el-button{
		border-radius: 0;
		border: none;
		width: 100%;
		background: #f1f1f1;
		border-right:  1px solid #e6e6e6;
	}
</style>


