import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index.vue'
import Signin  from '@/pages/signin'
import NotFound  from '@/pages/404'
import Work  from '@/components/Work'
import Task  from '@/components/Task'
import Project  from '@/components/Project'
import Member  from '@/components/Member'

Vue.use(Router)

export default new Router({
	mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      children:[
      	{
      		path:'/',
      		redirect: 'work'
      	},
      	{
      		path: 'work',
      		name: 'Work',
      		component: Work
      	},
      	{
      		path: 'task',
      		name: 'Task',
      		component: Task
      	},
      	{
      		path: 'project',
      		name: 'Project',
      		component: Project
      	},
      	{
      		path: 'member',
      		name: 'Member',
      		component: Member
      	}
      ]
    },
    {
      path: '/signin',
      name: 'Signin',
      component: Signin
    },
    {
    	path: '*',
    	name: 'NotFound',
    	component: NotFound
    }
  ]
})



